  import urls from '../../../utils/urls.js'
  export default {
    data() {
      return {
        current: 0,
				isLogin: this.$store.state.isLogin,
				NewUrl: urls.ImgUrl
      }
    },
    computed: {
      comments () {
        return this.$store.state.comments
      },
      id () {
        return JSON.parse(uni.getStorageSync('userInfo')).id
      }
    },
    methods: {
      seeTa: function (item) {
        console.log(item)
        this.$store.dispatch('otherUser', item.user)
        uni.navigateTo({
          url: 'otherUserInfo'
        })
      },
      thumbsUp: function (item) {
        // console.log(item)
        var id = item.id
				let id1 = '';
				if(this.isLogin){
					id1 = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id1 = 0
				}
        // var uid = JSON.parse(uni.getStorageSync('userInfo')).id
        var fuid = item.user.id
        this.current = id
        uni.request({
          url: urls.HTTP + '/comment/upcomment',
          method: 'POST',
          data: {
            cid: id,
            uid: id1,
            fuid: fuid
          },
          success: (res) => {
            // console.log(res)
            if (res.data.code === 0) {
              item.up++
            } else {
              uni.showToast({
                title: '请不要重复点赞',
                icon: 'none'
              })
            }
          }
        })
      },
      goDetail: function (val) {
        this.$store.dispatch('comment', val)
        uni.navigateTo({
          url: 'commontDetail'
        })
      },
      logoUrl: function (url) {
        return urls.UrlHttp + url
      },
      hasUp: function (i) {
        if (i === this.current) {
          return true
        } else {
          return false
        }
      }
    }
  }
